
import tabGroup from '@/components/group/tab'
import bigAnniu from '@/components/group/biganniu'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import { login } from '@/api/user'
import axios from 'axios'

export default {
components: {
tabGroup: tabGroup,
bigAnniu: bigAnniu,
XInput,
Group,
Grid,
GridItem
},
data () {
return {
anniu: '登录',
line: '请选择授权登录方式',
phone: '',
password: '',
code: '',
userInfo: {username: ''}
}
},
props: {},
computed: {},
methods: {
login () {
this.$router.push('/acLogin')
},
register () {
this.$router.push('/register')
},
orderhome () {
let params = new URLSearchParams()
params.append('userName', this.phone)
params.append('password', this.password)

login(params).then(response => {
this.user = response.data

this.$router.push({
path: '/',
query: { from: this.$route.query.index }
})





})
},

clear () {
this.phone = ''
},
clear1 () {
this.password = ''
},
changepassword () {
this.$router.push('/changepassword')
},
weiChatlogin () {
const url = 'https://open.weixin.qq.com/connect/oauth2/authorize?' +
'appid=wx67b189682f0cdbc0&redirect_uri=' + encodeURIComponent('http://sumuji.techwells.com/#/weixinlogin') +
'&response_type=code&scope=snsapi_userinfo&state=1324123#wechat_redirect';
window.location.replace(url);
}
},
mounted () {
}
}



